import React, { useState } from 'react';
import axios from 'axios';
import '../../Css/ChangePassword.css';

const ChangePassword = () => {
  // LocalStorage'dan kullanc bilgilerini al
  const userRole = localStorage.getItem("role")?.replace(/"/g, "") || "";
  const userId = localStorage.getItem("user_id")?.replace(/"/g, "") || "";
  
  console.log("User Role:", userRole); // "student" olmal
  console.log("User ID:", userId);

  const [formData, setFormData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  const [message, setMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [language, setLanguage] = useState('ko');

  const translations = {
    en: {
      title: "Change Password",
      currentPassword: "Current Password",
      newPassword: "New Password",
      confirmPassword: "Confirm Password",
      changePassword: "Change Password",
      passwordChanged: "Password changed successfully",
      passwordsNotMatch: "Passwords do not match",
      onlyStudents: "Only students can change password",
      fillAllFields: "Please fill all fields"
    },
    ko: {
      title: " ",
      currentPassword: " ",
      newPassword: " ",
      confirmPassword: " ",
      changePassword: " ",
      passwordChanged: "  ",
      passwordsNotMatch: "  ",
      onlyStudents: "    ",
      fillAllFields: "  "
    }
  };

  const t = translations[language];

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleLanguageChange = (lang) => {
    setLanguage(lang);
    setMessage('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.currentPassword || !formData.newPassword || !formData.confirmPassword) {
      setMessage(t.fillAllFields);
      return;
    }

    if (formData.newPassword !== formData.confirmPassword) {
      setMessage(t.passwordsNotMatch);
      return;
    }

    // DZELTME: userRole string olarak kontrol et
    if (userRole !== 'student') {
      setMessage(t.onlyStudents);
      return;
    }

    setIsLoading(true);
    setMessage('');

    try {
      const response = await axios.put('https://home-repair-api.onrender.com/api/users/change-password', {
        userId: userId, // userId'yi dorudan kullan
        currentPassword: formData.currentPassword,
        newPassword: formData.newPassword
      });

      if (response.data[language]) {
        setMessage(response.data[language]);
      } else if (response.data.en) {
        setMessage(response.data.en);
      } else {
        setMessage(t.passwordChanged);
      }

      setFormData({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      });
    } catch (error) {
      console.log('Error response:', error.response);
      
      let errorMessage = t.fillAllFields;
      
      if (error.response?.data) {
        if (typeof error.response.data === 'object') {
          errorMessage = error.response.data[language] || 
                        error.response.data.en || 
                        JSON.stringify(error.response.data);
        } else {
          errorMessage = error.response.data;
        }
      }
      
      setMessage(errorMessage);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="change-password-container">
      <div className="language-switcher">
        <button 
          className={`lang-btn ${language === 'en' ? 'active' : ''}`}
          onClick={() => handleLanguageChange('en')}
        >
          EN
        </button>
        <button 
          className={`lang-btn ${language === 'ko' ? 'active' : ''}`}
          onClick={() => handleLanguageChange('ko')}
        >
          KO
        </button>
      </div>

      <div className="change-password-card">
        <h2>{t.title}</h2>
        
        <form onSubmit={handleSubmit} className="password-form">
          <div className="form-group">
            <label htmlFor="currentPassword">{t.currentPassword}</label>
            <input
              type="password"
              id="currentPassword"
              name="currentPassword"
              value={formData.currentPassword}
              onChange={handleChange}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="newPassword">{t.newPassword}</label>
            <input
              type="password"
              id="newPassword"
              name="newPassword"
              value={formData.newPassword}
              onChange={handleChange}
              required
              minLength="6"
            />
          </div>

          <div className="form-group">
            <label htmlFor="confirmPassword">{t.confirmPassword}</label>
            <input
              type="password"
              id="confirmPassword"
              name="confirmPassword"
              value={formData.confirmPassword}
              onChange={handleChange}
              required
            />
          </div>

          {message && (
            <div className={`message ${
              message.includes('successfully') || 
              message.includes('') || 
              message.includes('') ? 'success' : 'error'
            }`}>
              {message}
            </div>
          )}

          <button 
            type="submit" 
            className="submit-btn"
            disabled={isLoading}
          >
            {isLoading ? '...' : t.changePassword}
          </button>
        </form>
      </div>
    </div>
  );
};

export default ChangePassword;