import React, { createContext, useContext, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const AuthContext = createContext();

export function AuthProvider({ children }) {
  const [user, setUser] = useState(() => {
    const token = localStorage.getItem('token');
    const storedUser = localStorage.getItem('user');
    return token && storedUser ? JSON.parse(storedUser) : null;
  });

  const navigate = useNavigate();

  // Kullanc giri fonksiyonu
  const login = async (email, password) => {
    try {
      const response = await axios.post('https://home-repair-api.onrender.com/api/auth/login', { email, password });
      const { user,role,user_id } = response.data; // Backend'den gelen user nesnesi
      
      // Kullanc bilgilerini localStorage'e kaydet
      localStorage.setItem('user_id', JSON.stringify(user_id));
      localStorage.setItem('role', JSON.stringify(role));

      setUser(user); // State'i gncelle

      // Kullancnn rolne gre ynlendirme yap
      if (user.role === 'student') {
        navigate('/');
      } else if (user.role === 'admin') {
        navigate('/');
      } else {
        navigate('/'); // Geerli rol yoksa ana sayfaya ynlendir
      }
    } catch (error) {
      console.error('Giri hatas:', error);
      throw new Error('Giri baarsz. Ltfen bilgilerinizi kontrol ediniz.');
    }
  };

  // Kullanc k fonksiyonu
  const logout = async () => {
    try {
      await axios.post('https://home-repair-api.onrender.com/api/auth/logout');

      // localStorage temizle
      localStorage.removeItem('role');
      localStorage.removeItem('user');
      localStorage.removeItem('user_id');

      setUser(null); // State sfrla

      navigate('/login'); // k sonras ynlendirme
    } catch (error) {
      console.error('k hatas:', error);
    }
  };

  return (
    <AuthContext.Provider value={{ user, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
}

// useAuth hook'u ile AuthContext'e eriim
export const useAuth = () => useContext(AuthContext);
